import styled from 'styled-components';

const AuthOverlay = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: #c7cfd5;
  opacity: 0.9;
`;

export default AuthOverlay;



