import styled from 'styled-components';

const MainWrapper = styled.div`
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  width: 100%;
  margin-bottom: 20px;
  padding: 10px 3% 0;
`;

export default MainWrapper;



