import styled from 'styled-components';
import { COLORS } from 'styles';

const FooterWrapper = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 15px 20%;
  background: ${COLORS.astral};
`;

export default FooterWrapper;



