import styled from 'styled-components';

const PortfolioWrapper = styled.section`
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin-top: 30px;
  padding: 2% 2%;
  background-color: #fff;
`;

export default PortfolioWrapper;



